import axios from "axios";
import { Dispatch } from "redux";
import { ActionTypes } from "./types";
import {
  ApplicationSessionInterface,
  SystemSettingsInterface,
} from "../shared/interfaces";
import { API } from "../utils/api";
import { setAxiosToken } from "../utils/AxiosToken";
import { errorToText } from "../utils/errors";
import { PositionAPI } from "../interfaces/position";

/**
 * * ****************************** INTERFACES *****************************
 */

// SYSTEM SETTINGS  ==============
export interface GetSystemSettingsAction {
  type: ActionTypes.GET_SYSTEM_SETTINGS;
  payload: SystemSettingsInterface[];
}

export interface DisableSystemSettingsAction {
  type: ActionTypes.DISABLE_SYSTEM_SETTINGS;
  payload: {
    system_setting_id: string;
    status: "enabled" | "disabled";
  };
}

// APPLICATION SETTINGS ===================================
export interface GetApplicationSessionAction {
  type: ActionTypes.GET_APPLICATION_SESSIONS;
  payload: ApplicationSessionInterface[];
}

export interface CreateApplicationSessionAction {
  type: ActionTypes.CREATE_APPLICATION_SESSIONS;
  payload: ApplicationSessionInterface;
}

export interface UpdateApplicationSessionAction {
  type: ActionTypes.UPDATE_APPLICATION_SESSIONS;
  payload: ApplicationSessionInterface;
}

export interface DisableApplicationSessionAction {
  type: ActionTypes.DISABLE_APPLICATION_SESSION;
  payload: {
    application_session_id: string;
    status: boolean;
  };
}

// POSITIONS SETTINGS ===================================
export interface PositionCreateInterface {
  position_id?: string;
  position_title: string;
  description: string;
  school_level_id: string;
  education_institute_id: string;
  courses: string[];
  requirements: {
    user_detail_category_id: string;
    user_detail_subcategory_id: string;
    position_requirement: {
      category_subcategory_data_id: string;
      required: boolean;
    }[];
    required: boolean;
  }[];
}
export interface GetPositionsAction {
  type: ActionTypes.GET_POSTIONS;
  payload: PositionAPI[];
}

export interface CreatePositionsAction {
  type: ActionTypes.CREATE_POSITION;
  payload: PositionCreateInterface;
}

export interface UpdatePositionsAction {
  type: ActionTypes.UPDATE_POSITION;
  payload: PositionCreateInterface;
}

export interface DeletePositionAction {
  type: ActionTypes.DELETE_POSITION;
  payload: string;
}

export interface PublishPositionAction {
  type: ActionTypes.PUBLISH_POSITION;
  payload: {
    position_id: string;
    status: boolean;
  };
}

export type SystemActionType =
  | GetSystemSettingsAction
  | DisableSystemSettingsAction
  | GetApplicationSessionAction
  | CreateApplicationSessionAction
  | UpdateApplicationSessionAction
  | DisableApplicationSessionAction
  | GetPositionsAction
  | CreatePositionsAction
  | UpdatePositionsAction
  | DeletePositionAction
  | PublishPositionAction;

/**
 * * ****************************** ACTIONS *****************************
 */

export const getSystemSettings = (callback: Function) => {
  return async (dispatch: Dispatch) => {
    callback(true, "");
    try {
      setAxiosToken();
      const res = await axios.get<SystemSettingsInterface[]>(
        `${API.AUTH}/settings`
      );
      dispatch<GetSystemSettingsAction>({
        type: ActionTypes.GET_SYSTEM_SETTINGS,
        payload: res.data,
      });
      callback(false, "");
    } catch (error) {
      console.log({ code_error: error });
      callback(false, errorToText(error));
    }
  };
};

export const disableSystemSetting = (data: {
  system_setting_id: string;
  status: "enabled" | "disabled";
}) => {
  return async (dispatch: Dispatch) => {
    dispatch<DisableSystemSettingsAction>({
      type: ActionTypes.DISABLE_SYSTEM_SETTINGS,
      payload: data,
    });
  };
};

