// CANDIDATE IN EDUCATION CATEGORY
export const getCandidatesInInstitute = (
  educatin_type_id: string,
  callback: Function
) => {
  console.log({
    url: `${API.APPLICATION}/educationtypeuserdocs/${educatin_type_id}`,
  });
  return async (dispatch: Dispatch) => {
    callback(true, "");
    try {
      setAxiosToken();
      const res = await axios.get<CandidatesInterface[]>(
        `${API.APPLICATION}/educationtypeuserdocs/${educatin_type_id}`
      );
      const newArray: CandidatesInterface[] = res.data.filter((element) => {
        return element !== null && element !== undefined;
      });
      console.log({ getCandidatesInInstitute: res, newArray });
      dispatch<GetCandidatesInInstituteAtion>({
        type: ActionTypes.GET_CANDIDATE_IN_INSTITUTE,
        payload: newArray,
        // payload: response.data,
      });
      callback(false, "");
    } catch (error) {
      console.log({ getCandidatesInInstitute: error });
      callback(false, errorToText(error));
    }
  };
};

