import React, { useState } from "react";
import { MdSearch } from "react-icons/md";
import { GetApplicationsAPI } from "../../../interfaces/application";

type FormApplicationListProps = {
  setChooseClaimFile: (value: React.SetStateAction<boolean>) => void;
  setClaimFileId: React.Dispatch<React.SetStateAction<string>>;
  setViewThisFile: React.Dispatch<React.SetStateAction<string>>;
  viewThisFile: string;
  candidateApplications: GetApplicationsAPI[];
};

export const searchApplication = (
  objList: GetApplicationsAPI[],
  text: any
): GetApplicationsAPI[] => {
  if (undefined === text || text === "") return objList;
  return objList.filter((product) => {
    let flag;
    // let dataKeys = product;

    for (let prop in product) {
      flag = false;
      if (prop === "position") {
        flag =
          product.position.position_title
            .toString()
            .toLowerCase()
            .indexOf(text.toLowerCase()) > -1;
      }
      if (flag) break;
    }
    return flag;
  });
  // return objList.filter((product) =>
  //   product.position.position_title.includes(text)
  // );
};

const FormApplicationList: React.FC<FormApplicationListProps> = (props) => {
  const [searchInput, setSearchInput] = useState<string>("");
  return (
    <section>
      {/* SEARCH PART */}
      <div className="flex items-center w-full my-1">
        <MdSearch className="w-6 h-6 absolute ml-2" />
        <input
          type="text"
          placeholder="Search"
          className="focus:border-2 h-7 ml-1 pl-9 mr-2 bg-gray-100 focus:border-gray-700 w-full rounded"
          onChange={(e) => setSearchInput(e.target.value)}
          value={searchInput}
        />
        <button
          className="bg-red-200 text-red-700 py-1 px-2 mr-1 rounded text-sm focus:outline-none"
          type="button"
          onClick={() => {
            props.setChooseClaimFile(false);
            props.setViewThisFile("");
          }}
        >
          Cancel
        </button>
      </div>
      {/* END SEARCH PART ///////////////// */}{" "}
      {/* searchData(props.candidateApplications, searchInput) */}
      <div className="w-full h-52 mt-2 overflow-y-auto divide-y divide-gray-300">
        {props.candidateApplications.length > 0 ? (
          searchApplication(props.candidateApplications, searchInput).map(
            (item, index) => (
              <div
                className="group flex items-center justify-between my-1 hover:bg-gray-100"
                key={index + item.application_id}
              >
                <div
                  className="p-1 w-full mr-2 cursor-pointer"
                  onClick={() => {
                    props.setChooseClaimFile(false);
                    props.setClaimFileId(item.application_id);
                  }}
                >
                  <p className="text-base font-medium text-black">
                    {item.position.position_title}
                  </p>
                </div>
                <button
                  type="button"
                  className="bg-blue-200 hover:bg-blue-300 text-blue-700 group-hover:text-blue-800 py-1 px-2 mr-1 rounded text-sm"
                  onClick={() => {
                    props.setViewThisFile(item.application_id);
                  }}
                >
                  View
                </button>
              </div>
            )
          )
        ) : (
          <div className="text-center p-3">
            <p className="text-base font-medium text-black">
              No Rejected document available
            </p>
          </div>
        )}
      </div>
    </section>
  );
};

export default FormApplicationList;
