import axios from "axios";
import { Dispatch } from "redux";
import { ActionTypes } from "./types";
import { API } from "../utils/api";
import { setAxiosToken } from "../utils/AxiosToken";
import { errorToText } from "../utils/errors";
import { GetApplicationsAPI } from "../interfaces/application";
import { CategorySubcategoryData } from "../interfaces/candidatesInterfaces";

export interface DocumentClaimUser {
  category_id: string;
  company: string;
  description: string;
  email: string;
  end_year: string;
  files: string;
  names: string;
  phone_number: string;
  school: {
    school_name: string;
    _id: string;
    status: boolean;
    education_category_id?: {
      education_category_title: string;
      _id: string;
    };
    district_id?: {
      district_name: string;
      _id: string;
    };
  } | null;
  school_id: string;
  start_year: string;
  sub_category_id: string;
  title: string;
  year: string;
  validation_status: string;
  validated_by: {
    user_id: string;
    comment: string;
    validation_status: string;
    date: string;
  };
  doc_id: string;
  category_subcategory_data: CategorySubcategoryData | null;
}

/**
 * * ****************************** INTERFACES *****************************
 */

// CANDIDATE_APPLICATIONS
export interface GetCandidateApplicationsAction {
  type: ActionTypes.GET_CANDIDATES_APPLICATIONS;
  payload: GetApplicationsAPI[];
}

// CANDIDATE_DOCUMENTS
export interface GetCandidateDocumentsAction {
  type: ActionTypes.GET_CANDIDATES_DOCUMENTS;
  payload: DocumentClaimUser[];
}

export type ClaimingActionType =
  | GetCandidateApplicationsAction
  | GetCandidateDocumentsAction;

/**
 * * ****************************** ACTIONS *****************************
 */

// PERSONAL APPLICATION
export const getCandidateApplications = (
  user_id: string,
  callback: Function
) => {
  callback(true, "");
  return async (dispatch: Dispatch) => {
    try {
      setAxiosToken();
      const res = await axios.get<GetApplicationsAPI[]>(
        `${API.APPLICATION}/application/${user_id}`
      );
      console.log({ claimPersonalApplication: res.data });
      let application: GetApplicationsAPI[] = res.data;
      if (res.status === 200) {
        // setApplications(res.data);
        if (res.data !== null && res.data.length > 0) {
          let dataRejected: GetApplicationsAPI[] = [];
          for (const key in res.data) {
            if (res.data[key].application_status === "rejected") {
              dataRejected.push(res.data[key]);
            }
          }
          application = dataRejected;
        }
      }
      dispatch<GetCandidateApplicationsAction>({
        type: ActionTypes.GET_CANDIDATES_APPLICATIONS,
        payload: application,
      });
      callback(false, "");
    } catch (error) {
      console.error(error);
      callback(false, errorToText(error));
    }
  };
};

// PERSONAL APPLICATION
export const getCandidateDocuments = (callback: Function) => {
  callback(true, "");
  return async (dispatch: Dispatch) => {
    try {
      setAxiosToken();
      const res = await axios.get<{
        n_id: string;
        fname: string;
        lname: string;
        district: string;
        phone_number: string;
        email: string;
        details: DocumentClaimUser[];
      }>(`${API.AUTH}/currentdet`);
      console.log({ claimPersonalDocuments: res.data });
      let details: DocumentClaimUser[] = [];
      if (res.status === 200) {
        // setApplications(res.data);
        if (res.data !== null && res.data.details.length > 0) {
          let dataRejected: DocumentClaimUser[] = [];
          for (const key in res.data.details) {
            if (res.data.details[key].validation_status === "rejected") {
              dataRejected.push(res.data.details[key]);
            }
          }
          details = dataRejected;
        }
      }
      dispatch<GetCandidateDocumentsAction>({
        type: ActionTypes.GET_CANDIDATES_DOCUMENTS,
        payload: details,
      });
      callback(false, "");
    } catch (error) {
      console.error(error);
      callback(false, errorToText(error));
    }
  };
};
