import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import { ClaimTypesInterface } from ".";
import { PushPopupAlert } from "../../../actions";
import { Auth } from "../../../actions/auth";
import {
  DocumentClaimUser,
  getCandidateApplications,
  getCandidateDocuments,
} from "../../../actions/claiming";
import {
  getDocumentCategory,
  getDocumentFields,
  getDocumentSubCategory,
} from "../../../actions/document";
import { PopupAlertData } from "../../../components/Alert/Popup";
import Modal, { ModalSize, Themes } from "../../../components/Modal/Modal";
import { GetApplicationsAPI } from "../../../interfaces/application";
import { PopupAlertTheme } from "../../../interfaces/popupAlert";
import { StoreState } from "../../../reducers";
import { ClaimingReducerType } from "../../../reducers/claiming";
import { DocumentReducerInterface } from "../../../reducers/document";
import { capitalizeFirstLetter } from "../../../shared/dataTest";
import ApplicationFormView from "./ApplicationFormView";
import DocFormView from "./DocFormView";
import FormApplicationList from "./FormApplicationList";
import FormDocumentList from "./FormDocumentList";

type ClaimFormProps = {
  // FOR SHORTCUT BUTTONS CALL
  claim_type?: ClaimTypesInterface;
  file_id?: string;
  formState: boolean;
  setFormState: (value: React.SetStateAction<boolean>) => void;
  auth: Auth;
  claimings: ClaimingReducerType;
  documentReducer: DocumentReducerInterface;
  getDocumentCategory: Function;
  getDocumentSubCategory: Function;
  getDocumentFields: Function;
  getCandidateApplications: Function;
  getCandidateDocuments: Function;
  PushPopupAlert: (alert: PopupAlertData) => void;
};

export const ClaimGetDocTitle = (
  documentCategories: DocumentReducerInterface,
  doc: DocumentClaimUser
): { category: string; sub_category: string; field: string; main: string } => {
  let DocCategory: string = "",
    DocSubCategory: string = "",
    DocField: string = "",
    DocTitle: string = "";
  if (
    doc.sub_category_id.length > 0 &&
    documentCategories.documentSubCategory
  ) {
    DocTitle = documentCategories.documentSubCategory.filter(
      (c) => c.user_detail_subcategory_id === doc.sub_category_id
    )[0].title;
    DocSubCategory = DocTitle;
  } else if (
    doc.category_id.length > 0 &&
    documentCategories.documentCategory
  ) {
    DocTitle = documentCategories.documentCategory.filter(
      (c) => c.user_detail_category_id === doc.category_id
    )[0].title;
    DocCategory = DocTitle;
  }
  if (
    doc.category_subcategory_data &&
    doc.category_subcategory_data.category_subcategory_Data_id.length > 0 &&
    documentCategories.documentFields
  ) {
    console.log({ doc });
    DocField = documentCategories.documentFields.filter(
      (c) =>
        c._id === doc.category_subcategory_data?.category_subcategory_Data_id
    )[0].title;
    DocTitle += " in " + DocField;
  }

  return {
    category: DocCategory,
    sub_category: DocSubCategory,
    field: DocField,
    main: DocTitle,
  };
};

const ClaimForm: React.FC<ClaimFormProps> = (props) => {
  const [loading, setLoading] = useState<boolean>(true);
  const [loadingApplications, setLoadingApplications] = useState<boolean>(true);
  const [loadingDocuments, setLoadingDocuments] = useState<boolean>(true);
  const [loadingForm, setLoadingForm] = useState<boolean>(false);
  const [formError, setFormError] = useState<{
    element: string;
    msg: string;
  } | null>(null);
  const [thisClaimType, setThisClaimType] = useState<string>("");
  const [claimComment, setClaimComment] = useState<string>("");
  const [claimFileId, setClaimFileId] = useState<string>("");
  const [chooseClaimFile, setChooseClaimFile] = useState<boolean>(false);
  const [viewThisFile, setViewThisFile] = useState<string>("");
  const [formType, setFormType] = useState<"DEFAULT" | "PROPS">("DEFAULT");
  const [alertError, setAlertError] = useState<string | null>(null);

  useEffect(() => {
    const isDocumentIdValid = (
      doc_id: string
    ): {
      status: boolean;
      msg: string;
    } => {
      if (props.claimings.candidateDocuments) {
        let doc = props.claimings.candidateDocuments.find(
          (item) => item.doc_id === doc_id
        );
        if (doc) {
          // CHECK IF DOCUMENT HAS ALREADY CLAIMED ON
          return { status: true, msg: "" };
        } else {
          return { status: false, msg: "Invalid document id" };
        }
      }
      return { status: false, msg: "No Documents" };
    };

    const isApplicationIdValid = (
      app_id: string
    ): {
      status: boolean;
      msg: string;
    } => {
      if (props.claimings.candidateApplications) {
        let doc = props.claimings.candidateApplications.find(
          (item) => item.application_id === app_id
        );
        if (doc) {
          // CHECK IF DOCUMENT HAS ALREADY CLAIMED ON
          return { status: true, msg: "" };
        } else {
          return { status: false, msg: "Invalid application id" };
        }
      }
      return { status: false, msg: "No Application, please try again!" };
    };

    if (!props.documentReducer.documentCategory) {
      props.getDocumentCategory();
    } else if (!props.documentReducer.documentSubCategory) {
      props.getDocumentSubCategory();
    } else if (!props.documentReducer.documentFields && loading) {
      props.getDocumentFields();
    }
    if (
      props.documentReducer.documentFields &&
      props.documentReducer.documentCategory &&
      props.documentReducer.documentSubCategory &&
      loading
    ) {
      setLoading(false);
    }

    if (props.claimings.candidateApplications === null) {
      props.getCandidateApplications(
        props.auth.user.user_id,
        (status: boolean, msg: string) => {
          if (status === false) {
            if (msg.length > 0) {
              props.PushPopupAlert({
                title: msg,
                theme: PopupAlertTheme.danger,
              });
            }
            setLoadingApplications(false);
          }
        }
      );
    } else {
      setLoadingApplications(false);
    }

    if (props.claimings.candidateDocuments === null) {
      props.getCandidateDocuments((status: boolean, msg: string) => {
        if (status === false) {
          if (msg.length > 0) {
            props.PushPopupAlert({
              title: msg,
              theme: PopupAlertTheme.danger,
            });
          }
          setLoadingDocuments(false);
        }
      });
    } else {
      setLoadingDocuments(false);
    }

    // PROPS VALIDATION PROCESS ###############
    if (props.claim_type && props.file_id && formType === "DEFAULT") {
      if (
        props.claim_type === ClaimTypesInterface.DOCUMENT &&
        !loadingDocuments
      ) {
        let _isDocumentIdValid = isDocumentIdValid(props.file_id);
        if (!_isDocumentIdValid.status) {
          // props.PushPopupAlert({
          //   title: _isDocumentIdValid.msg,
          //   theme: PopupAlertTheme.danger,
          // });
          setAlertError(_isDocumentIdValid.msg);
        } else {
          setThisClaimType(props.claim_type);
          setClaimFileId(props.file_id);
          setFormType("PROPS");
        }
      }
      if (
        props.claim_type === ClaimTypesInterface.APPLICATION &&
        !loadingApplications
      ) {
        let _isApplicationIdValid = isApplicationIdValid(props.file_id);
        if (!_isApplicationIdValid.status) {
          // props.PushPopupAlert({
          //   title: _isApplicationIdValid.msg,
          //   theme: PopupAlertTheme.danger,
          // });
          setAlertError(_isApplicationIdValid.msg);
        } else {
          setThisClaimType(props.claim_type);
          setClaimFileId(props.file_id);
          setFormType("PROPS");
        }
      }
    }
  }, [formType, loading, loadingApplications, loadingDocuments, props]);

  const getDocumentById = (
    documents: DocumentClaimUser[],
    doc_id: string
  ): DocumentClaimUser => {
    return documents.filter((item) => item.doc_id === doc_id)[0];
  };
  const getApplicationById = (
    applications: GetApplicationsAPI[],
    application_id: string
  ): GetApplicationsAPI => {
    return applications.filter(
      (item) => item.application_id === application_id
    )[0];
  };

  const handleSubmit = (e: React.FormEvent<HTMLFormElement>) => {};
  const cleanToClose = () => {
    props.setFormState(false);
    setClaimFileId("");
    setThisClaimType("");
    setClaimComment("");
    setChooseClaimFile(false);
    setFormType("DEFAULT");
    setViewThisFile("");
    setFormError(null);
    setViewThisFile("");
  };

  return (
    <div className="pb-3">
      {props.formState && (
        <Modal
          backDrop={true}
          theme={Themes.default}
          close={(): void => {
            !loadingForm && viewThisFile.length <= 0 && cleanToClose();
            !loadingForm && viewThisFile.length > 0 && setViewThisFile("");
          }}
          displayClose={true}
          backDropClose={false}
          title={
            <div className="text-gray-900 font-bold text-xl md:ml-1">
              {viewThisFile.length <= 0 && "Claiming form"}
              {viewThisFile.length > 0 &&
                thisClaimType === "document" &&
                "Viewing document"}
              {viewThisFile.length > 0 &&
                thisClaimType === "application" &&
                "Viewing application"}
            </div>
          }
          widthSizeClass={
            viewThisFile.length > 0 ? ModalSize.extraLarge : ModalSize.medium
          }
          padding={{ title: true, body: false, footer: false }}
        >
          {viewThisFile.length <= 0 && (
            <form onSubmit={(e) => handleSubmit(e)}>
              <div className="px-6 pb-3">
                {alertError && (
                  <div
                    className="alert-warning cursor-pointer"
                    onClick={() => setAlertError(null)}
                  >
                    {alertError}
                  </div>
                )}
                <div className="text-base text-gray-700 sm:text-lg">
                  <div className="flex flex-col mb-2">
                    <label>Claim type</label>
                    <select
                      className={`rounded px-2 py-2 w-full bg-white focus:outline-none ${
                        formError && formError.element === "claim_type"
                          ? "border-red-500"
                          : "border-gray-500"
                      } border-2`}
                      onChange={(e: React.ChangeEvent<HTMLSelectElement>) => {
                        setThisClaimType(e.target.value);
                        setClaimFileId("");
                        formError &&
                          formError.element === "claim_type" &&
                          setFormError(null);
                      }}
                      value={thisClaimType}
                      disabled={loadingForm || formType === "PROPS"}
                    >
                      <option value="">Choose claim type</option>
                      <option value={ClaimTypesInterface.DOCUMENT}>
                        {capitalizeFirstLetter(ClaimTypesInterface.DOCUMENT)}
                      </option>
                      <option value={ClaimTypesInterface.APPLICATION}>
                        {capitalizeFirstLetter(ClaimTypesInterface.APPLICATION)}
                      </option>
                      <option value={ClaimTypesInterface.PLACEMENT}>
                        {capitalizeFirstLetter(ClaimTypesInterface.PLACEMENT)}
                      </option>
                    </select>
                    {formError && formError.element === "claim_type" && (
                      <span className="text-red-500">{formError.msg}</span>
                    )}
                  </div>
                  <div className="flex flex-col mb-2">
                    <label>
                      Which {thisClaimType.length > 0 ? thisClaimType : "file"}{" "}
                      are you claiming on?
                    </label>
                    <div
                      className={`text-left bg-white border-2 border-gray-700 rounded w-full ${
                        chooseClaimFile ? "h-64" : "h-10"
                      } ${
                        formError && formError.element === "claim_file"
                          ? "border-red-500"
                          : "border-gray-500"
                      } border-2 ${
                        thisClaimType.length > 0 && !chooseClaimFile
                          ? "cursor-pointer"
                          : !chooseClaimFile && "cursor-not-allowed"
                      }`}
                      title={
                        thisClaimType.length <= 0
                          ? "Choose claim type first!"
                          : ""
                      }
                      onClick={() => {
                        formType === "DEFAULT" &&
                          thisClaimType.length > 0 &&
                          !chooseClaimFile &&
                          setChooseClaimFile(true);
                        formError &&
                          formError.element === "claim_file" &&
                          setFormError(null);
                      }}
                    >
                      {claimFileId.length > 0 && !chooseClaimFile ? (
                        <p className="my-1 mx-2">
                          {thisClaimType === "document" &&
                            props.claimings.candidateDocuments &&
                            ClaimGetDocTitle(
                              props.documentReducer,
                              getDocumentById(
                                props.claimings.candidateDocuments,
                                claimFileId
                              )
                            ).main}
                          {thisClaimType === "application" &&
                            props.claimings.candidateApplications &&
                            getApplicationById(
                              props.claimings.candidateApplications,
                              claimFileId
                            ).position.position_title}
                        </p>
                      ) : (
                        !chooseClaimFile && (
                          <p className="my-1 mx-2">Select Claim file</p>
                        )
                      )}

                      {chooseClaimFile &&
                        thisClaimType === "document" &&
                        props.claimings.candidateDocuments && (
                          <FormDocumentList
                            setChooseClaimFile={setChooseClaimFile}
                            setClaimFileId={setClaimFileId}
                            setViewThisFile={setViewThisFile}
                            viewThisFile={viewThisFile}
                            candidateDocuments={
                              props.claimings.candidateDocuments
                            }
                            documentCategories={props.documentReducer}
                          />
                        )}
                      {chooseClaimFile &&
                        thisClaimType === "application" &&
                        props.claimings.candidateApplications && (
                          <FormApplicationList
                            setChooseClaimFile={setChooseClaimFile}
                            setClaimFileId={setClaimFileId}
                            setViewThisFile={setViewThisFile}
                            viewThisFile={viewThisFile}
                            candidateApplications={
                              props.claimings.candidateApplications
                            }
                          />
                        )}
                    </div>
                    {formError && formError.element === "claim_file" && (
                      <span className="text-red-500">{formError.msg}</span>
                    )}
                  </div>
                  <div className="flex flex-col">
                    <label>Comment</label>
                    <textarea
                      className={`text-left input-white w-20 inline-block ${
                        formError && formError.element === "claim_comment"
                          ? "border-red-500"
                          : "border-gray-500"
                      } border-2`}
                      placeholder={`Claim comment`}
                      style={{ resize: "none" }}
                      value={claimComment}
                      onChange={(e) => {
                        setClaimComment(e.target.value);
                        formError &&
                          formError.element === "claim_comment" &&
                          setFormError(null);
                      }}
                      rows={3}
                      disabled={loadingForm}
                    ></textarea>
                    {formError && formError.element === "claim_comment" && (
                      <span className="text-red-500">{formError.msg}</span>
                    )}
                  </div>
                </div>
                <div className="pt-3 flex justify-between">
                  <button
                    onClick={() => cleanToClose()}
                    className={`btn bg-gray-400 text-white hover:bg-gray-600 focus:outline-none ${
                      loadingForm && "cursor-not-allowed"
                    }`}
                    type="button"
                    disabled={loadingForm}
                  >
                    Exit
                  </button>
                  <button
                    className={`btn bg-primary text-white hover:bg-blue-600 focus:outline-none ${
                      loadingForm && "cursor-wait"
                    }`}
                    type="submit"
                    disabled={loadingForm}
                  >
                    {loadingForm ? "Loading..." : "Claim"}
                  </button>
                </div>
              </div>
            </form>
          )}

          {viewThisFile.length > 0 &&
            props.claimings.candidateDocuments &&
            thisClaimType === "document" && (
              <DocFormView
                thisDoc={getDocumentById(
                  props.claimings.candidateDocuments,
                  viewThisFile
                )}
                setViewThisFile={setViewThisFile}
                documentCategories={props.documentReducer}
              />
            )}
          {viewThisFile.length > 0 &&
            props.claimings.candidateApplications &&
            thisClaimType === "application" && (
              <ApplicationFormView
                thisApplication={getApplicationById(
                  props.claimings.candidateApplications,
                  viewThisFile
                )}
                setViewThisFile={setViewThisFile}
              />
            )}
        </Modal>
      )}
    </div>
  );
};

const mapStateToProps = ({
  auth,
  document,
  claimings,
}: StoreState): {
  auth: Auth;
  documentReducer: DocumentReducerInterface;
  claimings: ClaimingReducerType;
} => {
  return {
    auth: auth,
    documentReducer: document,
    claimings,
  };
};

export default connect(mapStateToProps, {
  getCandidateApplications,
  getCandidateDocuments,
  getDocumentCategory,
  getDocumentSubCategory,
  getDocumentFields,
  PushPopupAlert,
})(ClaimForm);
