import React, { Fragment, useEffect, useState } from "react";
import { MdAdd } from "react-icons/md";
import { connect } from "react-redux";
import { PushPopupAlert } from "../../../actions";
import { Auth } from "../../../actions/auth";
import { getApplicationSessions } from "../../../actions/settings";
import { PopupAlertData } from "../../../components/Alert/Popup";
import Modal, { ModalSize, Themes } from "../../../components/Modal/Modal";
import { SideNavigation } from "../../../components/Navigation/SideNavigation";
import Table from "../../../components/Table/Table";
import { PopupAlertTheme } from "../../../interfaces/popupAlert";
import { StoreState } from "../../../reducers";
import { ApplicationSessionInterface } from "../../../shared/interfaces";
import ClaimHeaderTitle from "../ClaimHeaderTitle";
import FilterByType from "../FilterByType";
import ClaimForm from "./ClaimForm";

export enum ClaimTypesInterface {
  DOCUMENT = "document",
  APPLICATION = "application",
  PLACEMENT = "placement",
}

type AppProps = {
  auth: Auth;
  application_sessions: ApplicationSessionInterface[];
  getApplicationSessions: Function;
  PushPopupAlert: (alert: PopupAlertData) => void;
};

const Index: React.FC<AppProps> = (props) => {
  const [thisPeriod, setThisPeriod] = useState<string>("");
  const [thisClaimType, setThisClaimType] = useState<
    ClaimTypesInterface | "ALL"
  >("ALL");
  const [loading, setLoading] = useState<boolean>(true);
  const [loadingRequest, setLoadingRequest] = useState<boolean>(false);
  const [loadingSession, setLoadingSession] = useState<boolean>(true);
  const [loadingClaims, setLoadingClaims] = useState<boolean>(false);
  const [viewClaim, setViewClaim] = useState<boolean>(false);
  const [claimForm, setClaimForm] = useState<boolean>(false);

  useEffect(() => {
    setLoading(true);
    if (!props.application_sessions.length) {
      props.getApplicationSessions((status: boolean, msg: string) => {
        setLoadingSession(status);
        if (msg.length) {
          props.PushPopupAlert({
            title: msg,
            theme: PopupAlertTheme.danger,
          });
        }
      });
    } else {
      setLoading(false);
      setLoadingSession(false);
    }
  }, [props, thisPeriod, thisPeriod.length]);

  const tableData = (): JSX.Element[] => {
    return [
      <tr key={43243} className="border-b border-gray-200">
        <td className="p-3 text-center whitespace-nowrap">
          <div className="w-full h-4 rounded bg-gray-200 animate__animated animate__pulse animate__delay-5s animate__infinite"></div>
        </td>
        <td className="p-3 text-center whitespace-nowrap">
          <div className="w-full h-4 rounded bg-gray-200 animate__animated animate__pulse animate__delay-5s animate__infinite"></div>
        </td>
        <td className="p-3 text-center whitespace-nowrap">
          <div className="w-full h-4 rounded bg-gray-200 animate__animated animate__pulse animate__delay-5s animate__infinite"></div>
        </td>
        <td className="p-3 text-center whitespace-nowrap">
          <div className="w-full h-4 rounded bg-gray-200 animate__animated animate__pulse animate__delay-5s animate__infinite"></div>
        </td>
        <td className="p-3 text-center whitespace-nowrap">
          <div className="w-full h-4 rounded bg-gray-200 animate__animated animate__pulse animate__delay-5s animate__infinite"></div>
        </td>
      </tr>,
      <tr key={43243343} className="border-b border-gray-200">
        <td className="p-3 text-center whitespace-nowrap">
          <div className="w-full h-4 rounded bg-gray-200 animate__animated animate__pulse animate__delay-5s animate__infinite"></div>
        </td>
        <td className="p-3 text-center whitespace-nowrap">
          <div className="w-full h-4 rounded bg-gray-200 animate__animated animate__pulse animate__delay-5s animate__infinite"></div>
        </td>
        <td className="p-3 text-center whitespace-nowrap">
          <div className="w-full h-4 rounded bg-gray-200 animate__animated animate__pulse animate__delay-5s animate__infinite"></div>
        </td>
        <td className="p-3 text-center whitespace-nowrap">
          <div className="w-full h-4 rounded bg-gray-200 animate__animated animate__pulse animate__delay-5s animate__infinite"></div>
        </td>
        <td className="p-3 text-center whitespace-nowrap">
          <div className="w-full h-4 rounded bg-gray-200 animate__animated animate__pulse animate__delay-5s animate__infinite"></div>
        </td>
      </tr>,
      <tr key={4327543} className="border-b border-gray-200">
        <td className="p-3 text-center whitespace-nowrap">
          <div className="w-full h-4 rounded bg-gray-200 animate__animated animate__pulse animate__delay-5s animate__infinite"></div>
        </td>
        <td className="p-3 text-center whitespace-nowrap">
          <div className="w-full h-4 rounded bg-gray-200 animate__animated animate__pulse animate__delay-5s animate__infinite"></div>
        </td>
        <td className="p-3 text-center whitespace-nowrap">
          <div className="w-full h-4 rounded bg-gray-200 animate__animated animate__pulse animate__delay-5s animate__infinite"></div>
        </td>
        <td className="p-3 text-center whitespace-nowrap">
          <div className="w-full h-4 rounded bg-gray-200 animate__animated animate__pulse animate__delay-5s animate__infinite"></div>
        </td>
        <td className="p-3 text-center whitespace-nowrap">
          <div className="w-full h-4 rounded bg-gray-200 animate__animated animate__pulse animate__delay-5s animate__infinite"></div>
        </td>
      </tr>,
    ];
  };

  if (loading === true || loadingSession === true) {
    return (
      <div className="text-center mt-7 font-bold text-4xl text-gray-400 animate-pulse">
        Loading Data...
      </div>
    );
  }

  return (
    <Fragment>
      {/* SIDE NAVIGATION */}
      <SideNavigation>
        <button
          className="btn mb-3 border-2 border-blue-500 hover:bg-blue-500 hover:text-white flex items-center flex-1 font-bold w-full focus:outline-none"
          onClick={() => setClaimForm(true)}
          disabled={viewClaim || loadingRequest}
        >
          <MdAdd className="inline-block mr-1 text-xl font-bold" />
          <span>Claiming</span>
        </button>

        {/* FILTER SECTION */}
        <FilterByType
          thisClaimType={thisClaimType}
          setThisClaimType={setThisClaimType}
        />
        {/* END FILTER SECTION */}
      </SideNavigation>
      {/* END SIDE NAVIGATION */}

      {/* CLAIM TITLE ===================================== */}
      <ClaimHeaderTitle
        headerTitle={`${
          thisClaimType === "ALL"
            ? "All"
            : thisClaimType === ClaimTypesInterface.DOCUMENT
            ? "Documents"
            : thisClaimType === ClaimTypesInterface.APPLICATION
            ? "Applications"
            : thisClaimType === ClaimTypesInterface.PLACEMENT && "Placements"
        } Claiming`}
        loading={loading}
        loadingSession={loadingSession}
        thisPeriod={thisPeriod}
        setThisPeriod={setThisPeriod}
        setLoadingSession={setLoadingSession}
        application_sessions={props.application_sessions}
      />
      {/* END CLAIM TITLE ===================================== */}

      <section className="bg-white w-full p-1">
        <Table
          columns={[
            { columnTitle: "", alignText: "left", padding: "p-3" },
            { columnTitle: "", alignText: "left", padding: "p-3" },
            {
              columnTitle: "",
              alignText: "left",
              padding: "p-3",
            },
            {
              columnTitle: "",
              alignText: "left",
              padding: "p-3",
            },
            { columnTitle: "", alignText: "left", padding: "p-3" },
          ]}
          rows={tableData}
          pagination={true}
          search={true}
          thColor="gray"
          loading={loadingClaims}
        />
      </section>

      {/* modal control */}
      {viewClaim && (
        <Modal
          backDrop={true}
          theme={Themes.default}
          close={(): void => {
            setViewClaim(false);
          }}
          displayClose={true}
          backDropClose={false}
          title={
            <div className="text-gray-900 font-bold text-xl md:ml-1">
              Claiming form
            </div>
          }
          widthSizeClass={ModalSize.medium}
          padding={{ title: true, body: false, footer: false }}
        >
          <div className="mb-3">vIEW PART</div>
        </Modal>
      )}
      {/* end modal control */}

      {/* CLAIM FORM  ################ */}
      {claimForm && (
        <ClaimForm formState={claimForm} setFormState={setClaimForm} />
      )}
    </Fragment>
  );
};

const mapStateToProps = ({
  auth,
  system_settings,
}: StoreState): {
  auth: Auth;
  application_sessions: ApplicationSessionInterface[];
} => {
  return {
    auth: auth,
    application_sessions: system_settings.applicationSessions,
  };
};

export const CandidateClaim = connect(mapStateToProps, {
  getApplicationSessions,
  PushPopupAlert,
})(Index);
