import React, { useState } from "react";
import { MdSearch } from "react-icons/md";
import { DocumentReducerInterface } from "../../../reducers/document";
import { ClaimGetDocTitle } from "./ClaimForm";
import { DocumentClaimUser } from "../../../actions/claiming";
import { CategorySubcategoryData } from "../../../interfaces/candidatesInterfaces";

type FormDocumentListProps = {
  setChooseClaimFile: (value: React.SetStateAction<boolean>) => void;
  setClaimFileId: React.Dispatch<React.SetStateAction<string>>;
  setViewThisFile: React.Dispatch<React.SetStateAction<string>>;
  viewThisFile: string;
  candidateDocuments: DocumentClaimUser[];
  documentCategories: DocumentReducerInterface;
};

export interface DocumentTitleInterface {
  category_id: string;
  company: string;
  description: string;
  email: string;
  end_year: string;
  files: string;
  names: string;
  phone_number: string;
  school: {
    school_name: string;
    _id: string;
    status: boolean;
    education_category_id?: {
      education_category_title: string;
      _id: string;
    };
    district_id?: {
      district_name: string;
      _id: string;
    };
  } | null;
  school_id: string;
  start_year: string;
  sub_category_id: string;
  title: string;
  year: string;
  validation_status: string;
  validated_by: {
    user_id: string;
    comment: string;
    validation_status: string;
    date: string;
  };
  doc_id: string;
  doc_title: string;
  category_subcategory_data: CategorySubcategoryData | null;
}

export const DocumentTitleData = (
  documentData: DocumentClaimUser[],
  documentCategories: DocumentReducerInterface
): DocumentTitleInterface[] | [] => {
  let documentTemp: DocumentTitleInterface[] = [];
  for (let i in documentData) {
    documentTemp.push({
      category_id: documentData[i].category_id,
      company: documentData[i].company,
      description: documentData[i].description,
      email: documentData[i].email,
      end_year: documentData[i].end_year,
      files: documentData[i].files,
      names: documentData[i].names,
      phone_number: documentData[i].phone_number,
      school: documentData[i].school,
      school_id: documentData[i].school_id,
      start_year: documentData[i].start_year,
      sub_category_id: documentData[i].sub_category_id,
      title: documentData[i].title,
      year: documentData[i].year,
      validation_status: documentData[i].validation_status,
      validated_by: documentData[i].validated_by,
      doc_id: documentData[i].doc_id,
      doc_title: ClaimGetDocTitle(documentCategories, documentData[i]).main,
      category_subcategory_data: documentData[i].category_subcategory_data,
    });
  }
  return documentTemp;
};

export const searchDocuments = (
  objList: DocumentTitleInterface[],
  text: any
): DocumentTitleInterface[] => {
  if (undefined === text || text === "") return objList;
  return objList.filter(
    (product) =>
      product.doc_title.toString().toLowerCase().indexOf(text.toLowerCase()) >
      -1
  );
};

const FormDocumentList: React.FC<FormDocumentListProps> = (props) => {
  const [searchInput, setSearchInput] = useState<string>("");
  return (
    <section>
      {/* SEARCH PART */}
      <div className="flex items-center w-full my-1">
        <MdSearch className="w-6 h-6 absolute ml-2" />
        <input
          type="text"
          placeholder="Search"
          className="focus:border-2 h-7 ml-1 pl-9 mr-2 bg-gray-100 focus:border-gray-700 w-full rounded"
          onChange={(e) => setSearchInput(e.target.value)}
          value={searchInput}
        />
        <button
          className="bg-red-200 text-red-700 py-1 px-2 mr-1 rounded text-sm focus:outline-none"
          type="button"
          onClick={() => {
            props.setChooseClaimFile(false);
            props.setViewThisFile("");
          }}
        >
          Cancel
        </button>
      </div>
      {/* END SEARCH PART ///////////////// */}

      <div className="w-full h-52 mt-2 overflow-y-auto divide-y divide-gray-300">
        {props.candidateDocuments.length > 0 ? (
          searchDocuments(
            DocumentTitleData(
              props.candidateDocuments,
              props.documentCategories
            ),
            searchInput
          ).map((doc, index) => (
            <section key={index + doc.doc_id}>
              <div className="group flex items-center justify-between my-1 hover:bg-gray-100">
                <div
                  className="p-1 w-full mr-2 cursor-pointer"
                  onClick={() => {
                    props.setChooseClaimFile(false);
                    props.setClaimFileId(doc.doc_id);
                  }}
                >
                  <p className="text-base font-medium text-black">
                    {ClaimGetDocTitle(props.documentCategories, doc).main}
                  </p>
                </div>
                <button
                  type="button"
                  className="bg-blue-200 hover:bg-blue-300 text-blue-700 group-hover:text-blue-800 py-1 px-2 mr-1 rounded text-sm"
                  onClick={() => {
                    props.viewThisFile === doc.doc_id
                      ? props.setViewThisFile("")
                      : props.setViewThisFile(doc.doc_id);
                  }}
                >
                  {props.viewThisFile === doc.doc_id ? "Close" : "View"}
                </button>
              </div>
            </section>
          ))
        ) : (
          <div className="text-center p-3">
            <p className="text-base font-medium text-black">
              No Rejected document available
            </p>
          </div>
        )}
      </div>
    </section>
  );
};

export default FormDocumentList;
